/* RESET */
@import url("https://fonts.googleapis.com/css2?family=Fira+Sans:wght@100;200;300;400;500;800&display=swap");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body, html {
  width: 100100antialiased;
  -moz-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: none;
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
}

ol, ul {
  list-style: none;
}

hr {
  border: 0;
  height: 1px;
  background: #ccc;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
}

html, input, select, textarea, button, a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  text-decoration: none;
}

/* FONTS */
/* CONTAINER */
.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 auto;
}

@media (max-width: 1949px) {
  .container.container__only-big {
    width: 100%;
    max-width: 100%;
    padding-right: 0;
    padding-left: 0;
    margin: 0 auto;
  }
}

@media (max-width: 767px) {
  .container {
    padding-right: 30px;
    padding-left: 30px;
  }
}

@media (max-width: 575px) {
  .container:not(.container__only-big) {
    width: 100%;
    max-width: 100%;
  }
}

@media (max-width: 375px) {
  .container {
    padding-right: 20px;
    padding-left: 20px;
  }
}

@media (min-width: 576px) {
  .container:not(.container__only-big) {
    width: 90%;
    max-width: 100%;
  }
}

@media (min-width: 768px) {
  .container:not(.container__only-big) {
    width: 720px;
    max-width: 100%;
  }
}

@media (min-width: 992px) {
  .container:not(.container__only-big) {
    width: 960px;
    max-width: 100%;
  }
}

@media (min-width: 1200px) {
  .container:not(.container__only-big) {
    width: 1170px;
    max-width: 100%;
  }
}

@media (min-width: 1950px) {
  .container {
    width: 1920px;
    max-width: 100%;
  }
}

@keyframes path-to-shore {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes path-to-shore2 {
  from {
    stroke-dashoffset: -600;
  }
  to {
    stroke-dashoffset: 0;
  }
}

/* BASE */
body {
  font-family: 'Fira Sans', sans-serif;
  font-weight: 400;
  background-color: #171919;
}

.container {
  position: relative;
}

.to-action {
  display: flex;
  font-size: 40px;
  font-weight: 800;
  line-height: .9;
  color: #fff;
  text-transform: uppercase;
  cursor: default;
  user-select: none;
}

.to-action__first-half, .to-action__second-half {
  display: flex;
  align-items: center;
  padding: 0 18px;
}

.to-action__second-half {
  position: relative;
  height: 100%;
  background-color: #4AF6CD;
  color: #fff;
}

.to-action__second-half::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 20px;
  height: 1px;
  background-color: #fff;
  transform: translate(-50%, -50%);
}

.suptitle {
  display: inline-block;
  position: relative;
  font-size: 22px;
  line-height: 1.45;
  letter-spacing: 7.33px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.5);
}

.title {
  color: #fff;
  font-weight: 800;
  text-transform: uppercase;
}

.title_normal {
  line-height: .9;
  font-size: 40px;
}

.title_big {
  line-height: 1.075;
  font-size: 80px;
}

.title_page {
  line-height: 0.78;
  font-size: 200px;
  opacity: .02;
  transform: translateX(-49px);
  cursor: default;
  user-select: none;
}

.line {
  position: relative;
}

.line::before {
  content: '';
  position: absolute;
  width: 80px;
  height: 1px;
}

.line_left::before {
  left: 0;
  top: 50%;
  background-color: rgba(255, 255, 255, 0.5);
  transform: translate(calc(-100% - 24px), -50%);
}

.line_right::before {
  right: 0;
  top: 50%;
  background-color: rgba(255, 255, 255, 0.5);
  transform: translate(calc(100% + 24px), -50%);
}

.line_top::before {
  right: 0;
  top: 0;
  background-color: #4AF6CD;
}

.current-location {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.current-location__suptitle {
  margin-bottom: 8px;
}

.location {
  display: flex;
  line-height: 1.2;
  font-size: 40px;
  color: #fff;
  text-transform: uppercase;
}

.location__town, .location__country {
  font-weight: 800;
}

.location__divider {
  font-weight: 100;
}

.hero {
  width: 100%;
  height: 100vh;
  position: relative;
}

.hero__bg-title {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 467px;
  z-index: 1;
  opacity: .07;
}

.hero__aside {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

.hero__map {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.hero__bottom-panel {
  position: absolute;
  right: 80px;
  bottom: 31px;
}

.hero-map #path-to-shore-1, .hero-map #path-to-shore-2, .hero-map #path-to-shore-3, .hero-map #path-to-shore-4,
.hero-map #shore-1-active, .hero-map #shore-2-active, .hero-map #shore-3-active, .hero-map #shore-4-active,
.hero-map #shore-1-name, .hero-map #shore-2-name, .hero-map #shore-3-name, .hero-map #shore-4-name {
  display: none;
}

.hero-map #path-to-shore-1.show, .hero-map #path-to-shore-2.show, .hero-map #path-to-shore-3.show, .hero-map #path-to-shore-4.show,
.hero-map #shore-1-active.show, .hero-map #shore-2-active.show, .hero-map #shore-3-active.show, .hero-map #shore-4-active.show,
.hero-map #shore-1-name.show, .hero-map #shore-2-name.show, .hero-map #shore-3-name.show, .hero-map #shore-4-name.show {
  display: block;
}

.hero-map #path-to-shore-1 {
  stroke-dashoffset: 750;
  stroke-dasharray: 750;
  animation: path-to-shore 3s linear infinite 1s;
}

.hero-map #path-to-shore-2, .hero-map #path-to-shore-3, .hero-map #path-to-shore-4 {
  stroke-dashoffset: 600;
  stroke-dasharray: 600;
  animation: path-to-shore 3s linear infinite 1s;
}

.hero-aside {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 46px;
  width: 168px;
  height: 100%;
  border-right: 1px solid rgba(255, 255, 255, 0.25);
}

.hero-aside__logo {
  height: 168px;
  margin-bottom: 140px;
  font-size: 60px;
  line-height: 1.43;
  transform: rotate(-90deg) translateX(-124px);
}

.hero-aside__nav-menu {
  margin-bottom: 38px;
}

.hero-aside .nav-menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hero-aside .nav-menu__item {
  display: flex;
  justify-content: center;
}

.hero-aside .nav-menu__item-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
  letter-spacing: 3px;
  color: #fff;
  text-decoration: none;
}

.hero-aside .nav-menu__item-img {
  margin-bottom: 6px;
}

.hero-aside .nav-menu__item-text {
  display: block;
}

.hero-aside .nav-menu .nav-menu__item + .nav-menu__item {
  margin-top: 38px;
}

.hero-aside__search {
  display: flex;
  justify-content: center;
  margin-bottom: 38px;
}

.hero-aside__search img {
  width: 24px;
  height: 24px;
}

.hero-aside .aside-date {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
  color: #fff;
  cursor: default;
}

.hero-aside .aside-date__top-row {
  margin-bottom: 16px;
  font-size: 72px;
  font-weight: 800;
  line-height: .88;
  text-transform: uppercase;
}

.hero-aside .aside-date__bottom-row {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.18;
  letter-spacing: 1.14px;
}

.hero-aside .aside-date__month {
  margin-right: 14px;
}

.hero-aside .aside-date__year {
  position: relative;
}

.hero-aside .aside-date__year::before {
  content: '';
  position: absolute;
  width: 1px;
  height: 15px;
  background-color: #fff;
  left: -11px;
}

.hero-aside__location {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  cursor: default;
}

.hero-aside__location img {
  width: 11px;
  height: 13px;
  margin-right: 8px;
}

.hero-slider {
  position: relative;
}

.hero-slider__arrows {
  position: absolute;
  top: 40px;
  right: 80px;
  z-index: 2;
}

.hero-slider__arrows button {
  width: 25px;
  height: 20px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.hero-slider__arrows button:nth-of-type(1) {
  margin-right: 20px;
}

.hero-slider .slider-item {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  height: 100vh;
  padding: 40px 80px 116px;
  background: no-repeat center/cover;
}

.hero-slider .slider-item .slider-info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.hero-slider .slider-item .slider-info__sup-shore {
  margin-bottom: 5px;
}

.hero-slider .slider-item .slider-info__shore {
  margin-bottom: 48px;
}

.hero-slider .slider-item .slider-info__sup-condition {
  margin-bottom: 8px;
}

.hero-slider .slider-item .slider-info__condition {
  margin-bottom: 28px;
  line-height: 1.2;
}

.hero-slider .slick-slide {
  display: flex;
}

.bottom-panel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 289px);
  cursor: default;
}

.bottom-panel__scroll-down {
  width: 20px;
  height: 24px;
}

.bottom-panel__scroll-down img {
  animation-duration: 1.5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.bottom-panel .shores-list {
  display: flex;
}

.bottom-panel .shores-list .shores-item {
  color: #fff;
}

.bottom-panel .shores-list .shores-item_active .shores-item__line {
  background-color: #4AF6CD;
}

.bottom-panel .shores-list .shores-item__line {
  width: 100%;
  height: 4px;
  margin-bottom: 6px;
  background-color: #fff;
}

.bottom-panel .shores-list .shores-item__info {
  display: flex;
  align-items: center;
}

.bottom-panel .shores-list .shores-item__number {
  margin-right: 87px;
  font-size: 24px;
  font-weight: 800;
  line-height: 1.41;
  text-transform: uppercase;
}

.bottom-panel .shores-list .shores-item__name {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.18;
}

.bottom-panel .shores-list .shores-item + .shores-item {
  margin-left: 40px;
}

.surf {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 157px 80px 120px 0;
}

.surf__bg-title {
  position: absolute;
  top: 192px;
  left: 0;
}

.surf__title {
  display: inline-block;
  padding-left: 245px;
  margin-bottom: 70px;
}

.surf__geolocation-bg {
  position: absolute;
  top: 220px;
  right: 0;
  width: 295px;
  height: 350px;
  transform: translateX(50%);
}

.surf-slider {
  position: relative;
  padding-top: 110px;
  margin-right: -80px;
}

.surf-slider__arrows {
  position: absolute;
  top: 58px;
  right: 80px;
  z-index: 2;
}

.surf-slider__arrows button {
  width: 25px;
  height: 20px;
  border: none;
  opacity: .25;
  background-color: transparent;
  cursor: pointer;
}

.surf-slider__arrows button:nth-of-type(1) {
  margin-right: 20px;
}

.surf-slider .slider-item {
  position: relative;
  min-height: 750px;
  padding: 72px 0 0 40px;
  background: no-repeat center/cover;
}

.surf-slider .slider-item:nth-of-type(4n + 1) {
  margin-top: 32px;
}

.surf-slider .slider-item:nth-of-type(4n + 3) {
  margin-top: 89px;
}

.surf-slider .slider-item.slick-current .slider-item__overlay, .surf-slider .slider-item:hover .slider-item__overlay {
  transition: 1s;
  opacity: .3;
}

.surf-slider .slider-item.slick-current .slider-item__link, .surf-slider .slider-item:hover .slider-item__link {
  transition: 1s;
  opacity: 1;
}

.surf-slider .slider-item__overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #1C2121;
  opacity: .85;
}

.surf-slider .slider-item__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  min-height: 678px;
  z-index: 1;
}

.surf-slider .slider-item__title {
  word-spacing: 750px;
}

.surf-slider .slider-item__location {
  display: flex;
  position: absolute;
  left: 38px;
  bottom: 80px;
  color: #fff;
  font-family: Fira Sans;
  font-size: 18px;
  line-height: 1.22;
  letter-spacing: 8px;
  text-transform: uppercase;
  transform: rotate(-90deg) translateY(-100%);
  transform-origin: top left;
}

.surf-slider .slider-item__location .divider {
  font-weight: 100;
}

.surf-slider .slider-item__link {
  opacity: 0;
  height: 80px;
}

@media (max-width: 2200px) {
  .surf {
    overflow: hidden;
  }
